import React, { Component } from "react";
import Shuffle from "./Shuffle";
import "./Container.css";
import "./Button.css";
import axios from "axios";
import "./App.css";

var audioElement;
class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      english_text: "",
      vietnames_text: "",
      final_text: [],
      guessWord: "",
      checkAnswer: [],
      createAnswer: [],
      key: 0,
      index: 0,
      storeIndex: "",
      countGuess: 0,
      bool: false,
      english: "",
      vietnames: "",
      submit: true,
      value: "default",
      voice: "US English Male",
      valueInput: "",
      inputArr: [],
      urlDownLoad: "",
      urlYouTuBe: "",
      captions: [],
      duration: "",
      audioElement: "",
      starts: [],
      durations: [],
      count: 0,
      results: [],
      isPlayArr: [],
      step: 2,
      prevKey: -1,
      isEnd: false,
      isLoaded: false,
      defaultsYoutubeUrl: [
        "https://www.youtube.com/watch?v",
        "https://youtu.be/",
        "https://m.youtube.com/watch?feature=youtu.be&v=",
        "https://www.youtube.com/watch?feature=youtu.be&v=",
      ],
    };
    this.handleGuessButtons = this.handleGuessButtons.bind(this);
    this.handleGuessBox = this.handleGuessBox.bind(this);
    this.handleOtherButton = this.handleOtherButton.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.showAnswers = this.showAnswers.bind(this);
    this.handleSelectChange = this.handleSelectChange.bind(this);
    this.playAudio = this.playAudio.bind(this);
    this.getDefaulCaption = this.getDefaulCaption.bind(this);
    this.fetchEstimations = this.fetchEstimations.bind(this);
  }
  componentDidMount() {
    const script = document.createElement("script");

    script.src =
      "https://code.responsivevoice.org/responsivevoice.js?key=FdquDWj4";
    script.async = true;

    document.body.appendChild(script);
  }

  handleGuessBox(key) {
    // && this.state.guessWord.length > 0
    // let copy = this.state.isPlayArr.map((bool) => true);
    console.clear();
    if (key !== this.state.key) {
      this.setState((str) => ({
        //guessWord: "",
        key: key,
        index: 0,
        countGuess: 0,
        //   isPlayArr: copy,
      }));
    }
  }
  handleOtherButton(evt) {
    let ltr = evt.target.value;
    if (ltr !== "___") {
      //var msg = new SpeechSynthesisUtterance(ltr);
      //window.speechSynthesis.speak(msg);
      window.responsiveVoice.speak(ltr, this.state.voice);
    }
  }
  handleGuessButtons(evt) {
    let ltr;
    try {
      ltr = evt.target.value;
    } catch (err) {
      ltr = evt;
    }

    console.log(ltr);
    let {
      key,
      storeIndex,
      countGuess,
      checkAnswer,
      bool,
      createAnswer,
    } = this.state;
    const k = storeIndex[key];
    if (countGuess < 1) {
      //var msg = new SpeechSynthesisUtterance(ltr);
      // eslint-disable-next-line no-unused-expressions
      window.responsiveVoice.speak(ltr, this.state.voice);
      //window.speechSynthesis.speak(msg);
      // eslint-disable-next-line no-unused-expressions
    }

    let final_text = this.state.final_text.slice(
      0,
      this.state.final_text.length
    );
    if (
      checkAnswer[key][k].replace(",", "").toLowerCase() ===
      ltr.replace(",", "").toLowerCase()
    ) {
      let newAnswer = createAnswer.slice(0, createAnswer.length);
      if (countGuess > 1) {
        //msg = new SpeechSynthesisUtterance(ltr);
        //window.speechSynthesis.speak(msg);
        window.responsiveVoice.speak(ltr, this.state.voice);
      }
      final_text[key].splice(final_text[key].indexOf(checkAnswer[key][k]), 1);
      // final_text[this.state.key][final_text[this.state.key].indexOf(ltr)] =
      //   "__";
      if (countGuess !== 1 && bool) {
        //msg = new SpeechSynthesisUtterance(ltr);
        //window.speechSynthesis.speak(msg);
        //window.responsiveVoice.speak(ltr, this.state.voice);
      }
      newAnswer[key][k] = checkAnswer[key][k];
      let copy = storeIndex.slice(0, storeIndex.length);
      copy[key] = k + 1;
      this.setState((str) => ({
        createAnswer: newAnswer,
        index: k + 1,
        guessWord: this.state.checkAnswer[key][k],
        final_text: final_text,
        storeIndex: copy,
        countGuess: 0,
        bool: false,
        valueInput: "",
      }));
      if (k + 1 === checkAnswer[key].length) {
        this.setState((str) => ({
          index: 0,
        }));
      }
    } else {
      countGuess = countGuess + 1;
      if (countGuess >= 2) {
        //countGuess = 0;

        let text = `The next word is ${checkAnswer[key][k]}`;

        //window.speechSynthesis.speak(text);
        window.responsiveVoice.speak(text, this.state.voice);
        // alert(
        //   "Next Word : " +
        //     this.state.checkAnswer[this.state.key][
        //       this.state.storeIndex[this.state.key]
        //     ]
        // );
        this.setState((str) => ({
          valueInput: "",
        }));
      }

      this.setState((str) => ({
        guessWord: checkAnswer[key][k],
        countGuess: countGuess,
        bool: true,
      }));
    }
  }

  handleReadText(key) {
    if (this.state.final_text[key].length === 0) {
      window.responsiveVoice.speak(
        this.state.english_text[key],
        this.state.voice
      );
      // let msg = new SpeechSynthesisUtterance(this.state.english_text[key]);
      // msg.rate = 0.8;
      // //window.speechSynthesis.speak(msg);
    }
  }

  playAudio(index) {
    try {
      let {
        starts,
        durations,
        results,
        isPlayArr,
        prevKey,
        isEnd,
        step,
      } = this.state;
      let startTime, duration;
      if (index === results.length - 1) {
        startTime = starts[index * step];
        if (results.length % 2 !== 0) {
          duration = durations[index * step] + durations[index * step + 1] + 1;
        } else {
          duration = durations[index * step] + 1;
        }
        console.log(
          "start",
          parseInt(startTime / 60) + (startTime % 60) / 100,
          "duration",
          duration
        );
      } else {
        startTime = starts[index * step];
        duration = starts[index * step + step] - starts[index * step] - 0.15;
        console.log(
          "start",
          parseInt(startTime / 60) + (startTime % 60) / 100,
          "duration",
          duration
        );
      }

      let copy = isPlayArr.slice(0, isPlayArr.length);
      console.log("Text: ", results[index]);
      if (index !== prevKey && prevKey !== -1) {
        if (!copy[prevKey]) {
          copy[prevKey] = !copy[prevKey];
        }
        audioElement.pause();
      }
      if (!isPlayArr[index]) {
        try {
          audioElement.pause();
        } catch (error) {
          console.log("Don't Worry");
        }
      }
      copy[index] = !copy[index];
      if ((prevKey !== index && isPlayArr[index]) || isEnd === true) {
        try {
          audioElement.pause();
        } catch (error) {
          //
        }

        audioElement = document.createElement("video");

        audioElement.setAttribute("src", this.state.urlDownLoad);
        audioElement.value = duration;
        audioElement._startTime = startTime;
        audioElement.currentTime = startTime;
      }
      if (isEnd === true) isEnd = false;

      const self = this;
      audioElement.addEventListener("timeupdate", function () {
        //console.log(audioElement.currentTime);
        if (
          audioElement.currentTime - audioElement._startTime >=
          audioElement.value
        ) {
          audioElement.pause();
          isEnd = true;
        }
        if (isEnd) {
          let copy = isPlayArr.map((i) => true);
          self.setState({
            isPlayArr: copy,
            isEnd: isEnd,
          });
        }
      });

      if (isPlayArr[index]) {
        audioElement.play();
      }

      this.setState({
        isPlayArr: copy,
        prevKey: index,
        isEnd: isEnd,
      });
    } catch (error) {}
  }
  generateButtons() {
    let { final_text, createAnswer } = this.state;

    return final_text.map((elements, i) => {
      return (
        <section
          className="Container"
          key={i}
          value={i}
          onMouseEnter={() => this.handleGuessBox(i)}
        >
          {createAnswer[i].map((element, index) => (
            <button
              className="Button"
              key={index}
              value={element}
              onClick={this.handleOtherButton}
            >
              {element}
            </button>
          ))}

          {this.state.final_text[i].length > 0 ? (
            <div>
              <input
                type="text"
                name="valueInput"
                placeholder="Typing Answer"
                value={this.state.key === i ? this.state.valueInput : ""}
                onChange={(evt) => this.handleChange(evt, i)}
                style={{
                  borderRadius: "5px",
                  height: "30px",
                  width: "200px",
                  fontSize: "20px",
                }}
              />
            </div>
          ) : (
            <p></p>
          )}
          {elements.map((element, index) => (
            <button
              className="Button"
              key={index}
              value={element}
              onClick={this.handleGuessButtons}
            >
              {element}
            </button>
          ))}
          <div>
            <button
              className="Button"
              key={i}
              onClick={() => this.playAudio(i)}
              ref="play"
              onKeyUp={(evt) => this.handleKeyPress(evt, i)}
            >
              {this.state.isPlayArr[i] ? "Play" : "Pause"}
            </button>
            {/* <input
              type="hidden"
              // onKeyPress={(evt) => this.handleKeyPress(evt, i)}
              onKeyUp={(evt) => this.handleKeyPress(evt, i)}
            /> */}
          </div>

          {this.state.final_text[i].length === 0 ? (
            <div>
              <audio
                src="https://media.vocaroo.com/mp3/9EciEJW3Cyt"
                autoPlay
                controls
                hidden
              ></audio>
              <h1 id="meo">{this.state.results[i]}</h1>
            </div>
          ) : (
            <div>
              <button
                className="Button"
                style={{
                  background: "#2c3e50",
                  border: "none",
                  color: "#e2e5e9",
                }}
                key={i}
                onClick={() => this.showAnswers(i)}
              >
                Show Answer
              </button>
            </div>
          )}
        </section>
      );
    });
  }
  handleKeyPress = (event, i) => {
    let isEnd = this.state.isEnd;
    if (event.key === "Space") {
      this.playAudio(i);
    } else if (event.key === "ArrowLeft" && !isEnd) {
      audioElement.currentTime =
        audioElement.currentTime - 3 > audioElement._startTime
          ? audioElement.currentTime - 3
          : audioElement._startTime;
    } else if (event.key === "ArrowRight" && !isEnd) {
      audioElement.currentTime =
        audioElement.currentTime + 3 <
        audioElement._startTime + audioElement.duration
          ? (audioElement.currentTime += 3)
          : audioElement.currentTime;
    }
  };

  handleChange(evt, key) {
    let index = this.state.index;
    let checkAnswer = this.state.checkAnswer;
    let inputArr = this.state.inputArr;
    let copy = inputArr.slice(0, inputArr.length);
    let final_text = this.state.final_text;

    this.setState(
      {
        [evt.target.name]: evt.target.value,
      },
      function () {
        if (checkAnswer.length > 0) {
          let newCheckAnswer = final_text[key].map((s) =>
            s.replace(",", "").toLowerCase()
          );
          if (this.state.valueInput === "meowmeow") {
            window.responsiveVoice.speak("You say meowmeow", this.state.voice);
            if (this.state.final_text[key].length === 0) {
              this.setState({
                valueInput: "",
                index: index + 1,
              });
            } else {
              this.handleGuessButtons(checkAnswer[key][index]);
              this.setState({
                valueInput: "",
              });
            }
          }
          if (
            this.state.valueInput.trim().toLowerCase() ===
              checkAnswer[key][index].toLowerCase() &&
            this.state.final_text[key].length === 0
          ) {
            window.responsiveVoice.speak(
              this.state.valueInput,
              this.state.voice
            );
            if (index === checkAnswer[key].length - 1) {
              var audio = new Audio(
                "https://media.vocaroo.com/mp3/9EciEJW3Cyt"
              );
              audio.play();
              console.log("Yes");
              copy[key] = false;
            }

            this.setState({
              valueInput: "",
              index: index + 1,
              inputArr: copy,
            });
          } else if (
            newCheckAnswer.includes(
              this.state.valueInput.trim().toLowerCase()
            ) &&
            this.state.valueInput[this.state.valueInput.length - 1] === " "
          ) {
            this.handleGuessButtons(this.state.valueInput.trim().toLowerCase());
          }
        }
      }
    );
  }
  handleSelectChange(event) {
    let value = event.target.value;
    let voice = this.state.voice;
    let final_text = this.state.final_text.slice(
      0,
      this.state.final_text.length
    );
    let createAnswer = this.state.createAnswer.slice(
      0,
      this.state.createAnswer.length
    );
    switch (value) {
      case "showanswer":
        for (let i = 0; i < final_text.length; i++) {
          final_text[i] = [];
          createAnswer[i] = this.state.checkAnswer[i];
        }
        break;

      case "usmale":
        voice = "US English Male";
        break;
      case "usfemale":
        voice = "US English Female";
        break;
      case "ukmale":
        voice = "UK English Male";
        break;
      case "ukfemale":
        voice = "UK English Female";
        break;
      default:
        break;
    }
    this.setState({
      value: value,
      final_text: final_text,
      createAnswer: createAnswer,
      voice: voice,
    });
  }

  showAnswers(key) {
    let final_text = this.state.final_text.slice(
      0,
      this.state.final_text.length
    );
    let createAnswer = this.state.createAnswer.slice(
      0,
      this.state.createAnswer.length
    );

    final_text[key] = [];
    createAnswer[key] = this.state.checkAnswer[key];
    this.setState((str) => ({
      final_text: final_text,
      createAnswer: createAnswer,
    }));
  }

  async getDefaulCaption(shortUrl) {
    try {
      let captions = [];
      let starts = [];
      let durations = [];
      let deck = await axios.get(
        "https://video.google.com/timedtext?&lang=en&v=" + shortUrl
      );
      let string = deck.data
        .replace(new RegExp("&#39;", "g"), "'")
        .replace(new RegExp("&amp;#39;", "g"), "'")
        .replace(new RegExp("&amp;amp;", "g"), "and")
        .split("<transcript>");
      let result = string[1].split("</text>");
      result.pop();
      for (let i = 0; i < result.length; i++) {
        let arr = result[i].split(">");
        let textOfStartDuration = arr[0].split(" ");
        let start = textOfStartDuration[1].split("=")[1];
        let duration = textOfStartDuration[2].split("=")[1];

        captions.push(
          arr[1].replace(new RegExp('[&/\\#,+()$~%.":*?<>{}]', "g"), "")
        );
        starts.push(+start.replace(new RegExp('"', "g"), ""));
        durations.push(+duration.replace(new RegExp('"', "g"), ""));
      }
      let length = captions.length;
      let rawText = [];
      if (length % 2 === 0) {
        for (let i = 0; i < length; i += 2) {
          rawText.push(captions[i] + " " + captions[i + 1]);
        }
      } else {
        for (let i = 0; i < length - 1; i += 2) {
          rawText.push(captions[i] + " " + captions[i + 1]);
        }
        rawText.push(captions[length - 1]);
      }
      return {
        captions: captions,
        starts: starts,
        durations: durations,
        rawText: rawText,
      };
    } catch (error) {
      return false;
    }
  }

  async fetchEstimations(text, kind = "url") {
    try {
      const payload = new FormData();
      payload.append("text", text);
      const urlServer = "https://flaskforlistening.herokuapp.com/";
      const res = await fetch(urlServer + "api/" + kind, {
        method: "post",
        body: payload,
      });
      const estimation = await res.json();
      console.log(estimation);
      if (kind === "caption") {
        let starts = [];
        let durations = [];
        let captions = [];
        for (let i = 0; i < estimation.length; i++) {
          let value = estimation[i];
          starts.push(value["start"]);
          durations.push(value["duration"]);
          captions.push(value["text"]);
        }
        let length = captions.length;
        let rawText = [];
        const step = 4;
        if (length % 2 === 0) {
          for (let i = 0; i < length; i += step) {
            rawText.push(
              captions[i] +
                " " +
                captions[i + 1] +
                " " +
                captions[i + 2] +
                " " +
                captions[i + 3]
            );
          }
        } else {
          for (let i = 0; i < length - 1; i += step) {
            rawText.push(
              captions[i] +
                " " +
                captions[i + 1] +
                " " +
                captions[i + 2] +
                " " +
                captions[i + 3]
            );
          }
          rawText.push(captions[length - 1]);
        }
        this.setState({ step: step });
        return {
          captions: captions,
          starts: starts,
          durations: durations,
          rawText: rawText,
        };
      }
      return estimation;
    } catch (error) {
      return false;
    }
  }
  async handleSubmit(evt) {
    evt.preventDefault();
    let urlYouTuBe = this.state.urlYouTuBe.split("/");
    //let urlDownLoad = this.state.urlDownLoad;
    //if (urlYouTuBe.length > 0 && urlDownLoad.length > 0)
    if (urlYouTuBe.length > 0) {
      this.setState({ submit: false });
      if (
        this.state.captions.length === 0 &&
        this.state.urlYouTuBe.length > 0
      ) {
        urlYouTuBe = this.state.urlYouTuBe;
        let shortUrl = "";
        for (let i = 0; i < this.state.defaultsYoutubeUrl.length; i++) {
          if (!urlYouTuBe.indexOf(this.state.defaultsYoutubeUrl[i])) {
            switch (i) {
              case 0:
                shortUrl = urlYouTuBe.split("=")[1].split("&")[0];
                break;
              case 1:
                shortUrl = urlYouTuBe.split("/")[3].slice(0, 11);
                break;
              case 2:
                shortUrl = urlYouTuBe.split("=")[2];
                break;
              case 3:
                shortUrl = urlYouTuBe.split("&")[1].split("=")[1];
                break;

              default:
                break;
            }
          }
        }
        const url = this.state.defaultsYoutubeUrl[0] + "=" + shortUrl;
        let contents = await this.getDefaulCaption(shortUrl);
        let urlDownLoad = await this.fetchEstimations(url);
        let results, starts, durations, captions;

        if (contents === false) {
          contents = await this.fetchEstimations(shortUrl, "caption");
          if (contents === false) {
            alert("Please select a video with subtitles");
            return;
          }
        } else if (urlDownLoad === false) {
          alert("500 Internal Server Error");
          return;
        }

        results = contents["rawText"];
        starts = contents["starts"];
        durations = contents["durations"];
        captions = contents["captions"];

        let createAnswer = [];
        let inputArr = [];
        let storeIndex = [];
        let show = [];
        let checkAnswer = [];
        let final_text = [];
        let isPlayArr = [];
        for (let i = 0; i < results.length; i++) {
          let arr = [];
          checkAnswer.push(results[i].split(" "));
          final_text.push(results[i].split(" "));
          inputArr.push(true);
          storeIndex.push(0);
          isPlayArr.push(true);
          show.push(0);
          for (let j = 0; j < results[i].split(" ").length; j++) {
            arr.push("___");
          }
          createAnswer.push(arr);
        }
        final_text.forEach((element) => Shuffle(element));

        this.setState({
          captions: captions,
          urlDownLoad: urlDownLoad,
          starts: starts,
          durations: durations,
          results: results,
          final_text: final_text,
          createAnswer: createAnswer,
          inputArr: inputArr,
          storeIndex: storeIndex,
          show: show,
          checkAnswer: checkAnswer,
          isPlayArr: isPlayArr,
          isLoaded: true,
          //submit: false,
        });
      }
    }
  }

  render() {
    let content = this.generateButtons();
    if (this.state.guessWord === this.state.english_text[this.state.key]) {
      console.log("Your answer is correct");
    }
    //console.clear();
    return (
      <div className="App">
        {this.state.submit ? (
          <div id="form">
            {/* <iframe
              width="420"
              height="315"
              src="https://www.youtube.com/embed/Ye1ACgqbF_4"
            ></iframe> */}
            <form onSubmit={this.handleSubmit}>
              {/* <label>
                <input
                  type="text"
                  name="urlDownLoad"
                  placeholder="LinkDownLoad"
                  value={this.state.urlDownLoad}
                  onChange={this.handleChange}
                />
              </label> */}
              <label>
                <input
                  type="text"
                  name="urlYouTuBe"
                  placeholder="LinkYouTuBe"
                  value={this.state.urlYouTuBe}
                  onChange={this.handleChange}
                />
              </label>
              <button id="submit">Submit!</button>
            </form>
          </div>
        ) : (
          <div>
            {this.state.isLoaded ? (
              <div>
                <section id="meo">
                  <form onSubmit={this.showAnswers}>
                    <label>
                      <div className="select">
                        <select
                          name="slct"
                          id="slct"
                          value={this.state.value}
                          onChange={this.handleSelectChange}
                        >
                          <option value="default">Choose an option</option>
                          <option value="showanswer">Show Answers</option>
                          <option value="usmale">US English Male Voice</option>
                          <option value="usfemale">
                            US English FeMale Voice
                          </option>
                          <option value="ukmale">UK English Male Voice</option>
                          <option value="ukfemale">
                            UK English FeMale Voice
                          </option>
                        </select>
                      </div>
                    </label>
                  </form>
                </section>
                <p className="content-btns">{content}</p>
              </div>
            ) : (
              <div className="wrapper">
                <div className="rubik-loader"></div>
              </div>
            )}
          </div>
        )}
      </div>
    );
  }
}

export default App;
